      <div class="title">要放弃付款吗？</div>
      <!-- 优惠券内容 -->
      <div class="money-box">
        <!-- <div class="money">¥<span>187</span>元</div>
        <div class="money-topic">仅限今日使用</div> -->
      </div>
      <div class="btn-box">
        <div class="btn__notPay" @click="cancel">放弃支付</div>
        <div
          class="btn__pay"
          @click="confirm">继续支付 </div>
      </div>
    </div>
  </VanDialog>
</template>
<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    confirm () {
      this.$emit('input', false)
      this.$emit('confirm')
    },
    cancel () {
      this.$emit('input', false)
      this.$emit('cancel')
    }
  }
}
</script>

