<template>
  <div class="groupBC">
    <div class="header">
      <img
        src="@/assets/GuideClass/header_icon.png"
        alt=""
        class="title" />
      <div class="text">即将获得【理财新技能课】</div>
      <div class="actions">
        <img
          src="@/assets/GuideClass/logo.png"
          alt=""
          class="log" />
        <span class="name">微淼精品课</span>
        <img
          src="@/assets/GuideClass/open_icon.png"
          alt=""
          @click="jumpMini('106_tf_h5_00021')"
          class="btn" />
      </div>
    </div>
    <div class="content">
      <img
        src="@/assets/GuideClass/getClass.png"
        alt=""
        class="title" />
      <div class="item">
        <div class="text_title">
          <img src="@/assets/GuideClass/book_icon.png" alt="" /><span>方法1</span>
        </div>
        <div class="des">点击【立即打开】按钮，打开小程序，获取老师微信。</div>
      </div>
      <div class="item">
        <div class="text_title">
          <img src="@/assets/GuideClass/phone_icon.png" alt="" /><span>方法2</span>
        </div>
        <div class="des">
          点击<span style="color: #1d53df; font-weight: 500">【一键复制】</span>，关注微淼精品课公众号后，根据指示添加老师微信。
        </div>
      </div>
      <div class="btn" @click="copy">一键复制</div>
    </div>
    <VanDialog
      class="dialog"
      v-model="show"
      :show-confirm-button="false"
      :close-on-click-overlay="true">
      <div class="dialog_content">
        <img
          src="@/assets/GuideClass/close.png"
          alt=""
          @click="toClose"
          class="close" />
        <img
          src="@/assets/GuideClass/light.png"
          alt=""
          class="light" />
        <div class="dialog_title">温馨提示</div>
        <div class="msg">您已经报名课程，不添加老师微信，无法上课哦~！</div>
        <div class="dialog_btn" @click="jumpMini('106_tf_h5_00024')">获取老师微信</div>
      </div>
    </VanDialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getSearch, copyText } from '@/utils'
import { wmAppid } from '@/config'
import preventBack from '@/mixins/preventBack'
export default {
  mixins: [preventBack],
  data () {
    return {
      show: false
    }
  },
  computed: {
    ...mapState('ABTest', ['abData'])
  },
  created () {
    console.log(this.abData)
  },
  mounted () {
    this.$on('userBack', () => {
      this.show = true
      this.$log.show({
        module_ori: '挽留弹窗',
        module_part: '106_tf_h5_00023'
      })
    })
  },
  methods: {
    toClose () {
      this.show = false
      this.$log.show({
        module_ori: '挽留弹窗',
        module_part: '106_tf_h5_00025'
      })
    },
    async jumpMini (part) {
      // 根据不同得组跳转不同页面
      this.$log.click({
        module_ori: '基于页面',
        module_part: part
      })
      let path = 'pages/index/index'
      const phone = localStorage.getItem('guide_phone')
      if (this.abData.ab_group === 'C') {
        path = 'pages/officalQrcode/index'
      }
      const query = { ...getSearch(), ...this.abData, phone }
      const { url } = await this.$apis.userOperator.getMiniHref({
        path,
        appid: wmAppid,
        query
      })
      window.location.href = url
    },
    copy () {
      copyText('微淼精品课')
      this.$log.click({
        current_page: location.href,
        module_ori: '基于页面',
        module_part: '106_tf_h5_00022'
      })
      window.location.href = 'weixin://'
    }
  }
}
</script>
