<template>
  <div class="gdt">
    <img
      :src="headerImg"
      alt="" />
    <div class="formPay">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请输入手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix">+86<span class="icon">▼</span></span>
          </template>
        </VanField>
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <div class="btn" @click="submit">立即抢购</div>
      </VanForm>
    </div>
    <div class="tips">*注：抢购后，班主任将全程指导学习</div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>报名成功</p>
          <p>{{ el.time }}秒前</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <img
      v-for="(url, k) in detailImg"
      :key="k"
      :src="url"
      alt="" />
    <div class="copyright">
      <p>北京微淼财商科技有限公司</p>
      <p>京ICP备19026951号</p>
    </div>
    <div class="footer">
      <div>
        <p style="text-align: center; font-size: 16px; font-weight: bold;">
          仅售
          <span
            style="font-size: 22px; color: #ff2c12; font-weight: bold;">12元</span>
        </p>
      </div>
      <a class="submitBtn" @click="submit">立即抢购</a>
    </div>
    <BackDiaLog
      @confirm="pay"
      v-model="showDialog" />
  </div>
</template>
<script>
import CodeMap from './ErrorMsg.js'
import { getSearch, calArray, isWeiXin } from '@/utils'
import BackDiaLog from '@/components/common/StayDialog' // 支付挽留弹框
import CountDown from '@/components/common/CountDown'
import wxAuth from '@/helper/Auth/wxAuth'
export default {
  components: {
    BackDiaLog,
    CountDown
  },
  data: function () {
    return {
      swipeList: [],
      form: {},
      showDialog: false,
      headerImg: 'http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/HeadlineA/header.png',
      detailImg: [
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/1487phpGM1OP91600075994200914.png',
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/3202phpaQyTvW1615620424210313.png',
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/6723phpgXq0hZ1615620430210313.png',
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/6397php1OK9fG1600076003200914.png',
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/4123phpw0RGT11600076006200914.png',
        'https://oss-market.weimiaocaishang.com/Public/infoflow-admin/8301phpJhoUxO1600076009200914.png'
      ]
    }
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('ott-phone'),
      code: localStorage.getItem('ott-code')
    }
    this.checkOrderInWx()
    const res = await this.$apis.userOperator.getLatestUser()
    this.swipeList = calArray(res, 2)
  },
  async beforeRouteEnter (to, form, next) {
    if (!isWeiXin()) {
      return next({
        name: 'OttH5',
        query: {
          ...to.query,
          medId: 1114,
          push_id: 'A1_C2_C3'
        }
      })
    }
    // 获取微信授权
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  methods: {
    // 提交表单
    submit () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_tf_h5_00042'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code, data } = err
      let confirmButtonText = '确定'
      if (err.code === 30009) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || CodeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30009) {
          this.goSucccess(data.product_sn)
        }
      })
    },
    // 跳转成功页面
    goSucccess (sn) {
      this.$router.push({
        name: 'QrcodeInWx',
        query: {
          productSn: sn
        }
      })
    },
    // 查询订单
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_XIAOBAI_12' })
        .catch((err) => {
          this.showMsg(err)
          return Promise.reject(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_tf_h5_00043'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('ott-phone', this.form.phone)
      localStorage.setItem('ott-code', this.form.code)
      this.pay()
    },
    // 支付
    async pay () {
      this.showDialog = false
      const sn = 'PRODUCT_SN_XIAOBAI_12'
      this.$apis.payment.payInWx({
        pay_type: 2,
        pay_mode: 3,
        ...getSearch(),
        ...this.form,
        product_sn: sn
      })
        .then(result => {
          window.WeixinJSBridge.invoke('getBrandWCPayRequest', result, (res) => {
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              this.goSucccess(sn)
            } else {
              this.showDialog = true
            }
          })
        })
        .catch(err => {
          this.showMsg(err)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.gdt {
  padding-bottom: 106px;

  .zero__line-title {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
  }

  .zero__swipe-box {
    margin-left: 72px;
    height: 100px;
    margin-top: 30px;

    .zero__swipe-box__item {
      width: calc(100% - 100px);
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      font-weight: 400;
      line-height: 50px;
      color: #434c5d;
    }
  }

  .footer {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 106px;

    p {
      line-height: 106px;
    }
  }

  .submitBtn {
    display: block;
    width: 60%;
    height: 100%;
    font-family: PingFangSC-Semibold;
    font-size: 46px;
    color: #fffcff;
    line-height: 106px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    background: linear-gradient(to left, #ff5e00, #ff1b00);
  }

  .footer div {
    width: 40%;
    height: 100%;
    background-color: #fff;
    box-sizing: border-box;
  }

  .minge,
  .person {
    font-family: PingFangSC-Regular;
    font-size: 0.24rem;
    color: #333;
    text-align: center;
  }

  .person span {
    font-family: PingFangSC-Semibold;
    font-size: 0.4rem;
    color: #ff2c12;
    text-align: center;
  }

  img {
    width: 100%;
    vertical-align: top;
  }

  .formPay {
    box-sizing: border-box;
    padding: 20px 75px;
    text-align: center;

    .wrap {
      position: relative;
    }

    .btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 600px;
      height: 108px;
      background: linear-gradient(to left, #ff5e00, #ff1b00);
      background: -webkit-linear-gradient(to left, #ff5e00, #ff1b00);
      background: -o-linear-gradient(to left, #ff5e00, #ff1b00);
      background: -moz-linear-gradient(to left, #ff5e00, #ff1b00);
      border-radius: 60px;
      font-size: 32px;
      font-family: Source Han Sans CN;
      color: #fff;
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      font-size: 32px;
      font-weight: 500;
      line-height: 32px;
      font-family: PingFangSC-Regular;
    }

    /deep/ .van-cell {
      .prefix {
        font-size: 32px;
        font-weight: 500;
        color: rgb(45, 45, 45);
        display: flex;
        align-items: center;

        .icon {
          font-size: 10px;
          margin: 0 10px;
          color: #ccc;
        }

        padding-right: 15px;
      }

      margin-bottom: 20px;
      width: 600px;
      height: 86px;
      background: #f5f8ff;
      border-radius: 14px;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;
      border: 1px solid rgba(204, 204, 204, 1);

      &::after {
        display: none;
      }

      .van-field__control::placeholder {
        font-size: 28px;
        font-weight: 400;
      }

      .van-count-down {
        font-size: 32px;
        font-weight: 500;
        color: #ff1b00 !important;
      }

      .van-field__button {
        width: 200px;
      }
    }
  }

  .tips {
    text-align: center;
    padding-top: 16px;
    color: #ccc;
  }

  .copyright {
    padding: 20px 0;
    text-align: center;
    color: #333;
    font-weight: 700;

    p {
      padding: 10px 0;
    }
  }
}
</style>
